import axios from 'axios';

//Ly tt c nhn vin
export const getAllEmployeeApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/Employee"
    })
    return res.data;
};

//Ly m nhn vin ln nht
export const getEmployeeMaxCodeApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/Employee/getEmployeeCodeMax"
    })
    return res.data;
};

//Thm mt nhn vin 
export const addOneEmployeeApi=async (employee)=>{
    const res=await axios({
        method:"POST",
        url:"https://localhost:44363/api/Employee",
        data:employee
    })
    return res.data
};

//Xa mt nhn vin
export const deleteOneEmployeeApi=async (employeeID)=>{
    const res=await axios({
        method:"DELETE",
        url:"https://localhost:44363/api/Employee/"+`${employeeID}`,
    })
    return res.data;
};

//Cp nht mt nhn vin
export const updateOneEmployeeApi=async (employee)=>{
    const res=await axios({
        method:"PUT",
        url:"https://localhost:44363/api/Employee",
        data:employee
    })
    return res.data;
};