import axios from 'axios';

//Ly tt c ho n
export const getAllSaleBillApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/SaleBill"
    })
    return res.data;
};

//Ly chi tit ho n
export const getSaleBillDetailApi=async (saleBill_ID)=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/SaleBill/getSaleBillDetail/"+saleBill_ID
    })
    return res.data;
};

//Ly m ho n ln nht
export const getSaleBillMaxCodeApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/SaleBill/getSaleBillCodeMax"
    })
    return res.data;
};

//Thm mt ho n
export const addOneSaleBillApi=async (SaleBill)=>{
    const res=await axios({
        method:"POST",
        url:"https://localhost:44363/api/SaleBill",
        data:SaleBill
    })
    return res.data
};

//Xa mt ho n
export const deleteOneSaleBillApi=async (SaleBill_ID)=>{
    const res=await axios({
        method:"DELETE",
        url:"https://localhost:44363/api/SaleBill/"+`${SaleBill_ID}`,
    })
    return res.data;
};

//Cp nht mt ho n
export const updateOneSaleBillApi=async (SaleBill)=>{
    const res=await axios({
        method:"PUT",
        url:"https://localhost:44363/api/SaleBill",
        data:SaleBill
    })
    return res.data;
};