import axios from 'axios';

//Ly tt c mn n
export const getAllFoodApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/food"
    })
    return res.data;
};

//Ly m mn n ln nht
export const getFoodMaxCodeApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/food/getFoodCodeMax"
    })
    return res.data;
};

//Thm mt mn n
export const addOneFoodApi=async (food)=>{
    const res=await axios({
        method:"POST",
        url:"https://localhost:44363/api/food",
        data:food,
        headers: { "Content-Type": "multipart/form-data" },
    })
    return res.data
};

//Xa mt mn n
export const deleteOneFoodApi=async (foodID)=>{
    const res=await axios({
        method:"DELETE",
        url:"https://localhost:44363/api/food/"+`${foodID}`,
    })
    return res.data;
};

//Cp nht mt th loi mn n
export const updateOneFoodApi=async (food)=>{
    const res=await axios({
        method:"PUT",
        url:"https://localhost:44363/api/food",
        data:food,
        headers: { "Content-Type": "multipart/form-data" },
    })
    return res.data;
};

