import axios from 'axios';

//Ly tt c th loi mn n
export const getAllFoodCategoryApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/foodCategory"
    })
    return res.data;
};

//Ly m th loi mn n ln nht
export const getFoodCategoryMaxCodeApi=async ()=>{
    const res=await axios({
        method:"GET",
        url:"https://localhost:44363/api/foodCategory/getFoodCategoryCodeMax"
    })
    return res.data;
};

//Thm mt th loi mn n
export const addOneFoodCategoryApi=async (foodCategory)=>{
    const res=await axios({
        method:"POST",
        url:"https://localhost:44363/api/foodCategory",
        data:foodCategory
    })
    return res.data
};

//Xa mt th loi mn n
export const deleteOneFoodCategoryApi=async (foodCategoryID)=>{
    const res=await axios({
        method:"DELETE",
        url:"https://localhost:44363/api/foodCategory/"+`${foodCategoryID}`,
    })
    return res.data;
};

//Cp nht mt th loi mn n
export const updateOneFoodCategoryApi=async (foodCategory)=>{
    const res=await axios({
        method:"PUT",
        url:"https://localhost:44363/api/foodCategory",
        data:foodCategory
    })
    return res.data;
};