//Hm format gi tr sang dng tin t
var formatter = new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'VND'
});

//Hm format gi tr thnh gii tnh
const filterGender=(value)=>{
    switch (value)
            {
                case 0 : {
                    return "Nam";
                }
                case 1 : {
                    return "N"
                }
                default : {
                    return ''
                }
    }
}

//Hm nh dng trng thi ho n bn
const formatStatus_SB=(value)=>{
    switch (value)
            {
                case 0 : {
                    return "Cha giao hng";
                }
                case 1 : {
                    return " giao hng"
                }
    }
}

export {
    filterGender,
    formatter,
    formatStatus_SB
}